import { useEffect, useRef } from "react";
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";

// import required modules
import { useState } from "react";
import { IoStatsChart } from "react-icons/io5";
import { Autoplay, Navigation } from "swiper";
import http from "./../api/index";
import { AiFillCaretDown, AiFillCaretUp } from "react-icons/ai";
import { useHistory } from "react-router-dom";

//sample data
export default function  CarPriceCarousel  ()  {
  const api = http();
  const [loaded, setLoaded] = useState(false);
  const [data, setData] = useState([]);
  const prevRef = useRef(null);
  const nextRef = useRef(null);
  const history = useHistory();
  useEffect(() => {
    api.GetLastCarPrice(({ data }) => {
      var _data = [];
      // eslint-disable-next-line array-callback-return
      data.map((brand) => {
        // eslint-disable-next-line array-callback-return
        brand.carPriceItems.map((item) => {
          _data.push({
            ...item,
            title: brand.title,
            iconName: brand.iconName,
          });
        });
      });
      setData([..._data]);
      setLoaded(true);
    });
    return () => {};
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="col-12">
      <div className=" col-12 justify-content-around align-items-center car-price-container new-shadow">
        <div className="d-flex  justify-content-between mb-3">
          <div className="d-flex align-items-center ">
            <span className="custom-chart">
              <IoStatsChart fontSize={25} />
            </span>
            <h4>قیمت روز خودرو</h4>
          </div>

          <span
            onClick={() => history.push("/carPrice")}
            className=" text-center justify-items-center aligh-items-center col-3 col-md-2 col-lg-1 nbt p-3"
          >
            مشاهده بیشتر
          </span>
        </div>
        {loaded ? (
          <Swiper
            navigation={{
              // Both prevEl & nextEl are null at render so this does not work
              prevEl: nextRef.current,
              nextEl: prevRef.current,
            }}
            autoplay={{
              delay: 1500,
            }}
            translate="no"
            slidesPerView={4}
            spaceBetween={15}
            modules={[Navigation, Autoplay]}
            loopFillGroupWithBlank={true}
            breakpoints={{
              320: {
                slidesPerView: 2,
                spaceBetween: 20,
              },
              // when window width is >= 480px
              480: {
                slidesPerView: 3,
                spaceBetween: 20,
              },
              620: {
                slidesPerView: 4,
                spaceBetween: 20,
              },
              // when window width is >= 640px
              1200: {
                slidesPerView: 6,
                spaceBetween: 20,
              },
              1400: {
                slidesPerView: 7,
                spaceBetween: 20,
              },
            }}
            loop={true}
          >
            {data.map((_item, key) => (
              <SwiperSlide
                className=""
                style={{ backgroundColor: "transparent" }}
                key={_item.id}
              >
                <figure className="text-center ">
                  <span className={_item.iconName}></span>
                </figure>
                <h6 className="text-center mt-1">{_item.name} </h6>
                <h6 className="text-center mt-1">{_item.model}</h6>
                <h6 className="text-center mt-1">
                  <span
                    className={`
                 ${_item.priceStatus === "up" ? "text-success" : ""}
                 ${_item.priceStatus === "down" ? "text-danger" : ""}
                `}
                  >
                    {_item.pricePercent}
                    {_item.priceStatus === "up" ? (
                      <AiFillCaretUp size={15} />
                    ) : null}
                    {_item.priceStatus === "down" ? <AiFillCaretDown /> : null}
                  </span>{" "}
                  || {_item.price}
                </h6>
              </SwiperSlide>
            ))}
          </Swiper>
        ) : (
          <div class="text-center text-dark">
            <div class="spinner-border " role="status">
              <span class="sr-only"></span>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};
